@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.bodyLogin {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 10%;
}

.login {
    display: flex;
    flex-direction: column;
    background-color: #DFE2E5;
    width: 35%;
    border: 1px solid #A4C3D8 !important;
    border-radius: 5px;
    align-items: center;
    
}

.formsLogar input {
    margin-bottom: 5px;
    width: 95%;
}



.formsLogar {
    display: flex;
    flex-direction: column;
    margin: 5%;
    width: 90%;

}

.machineImg {
    width: 30%;
}

.logoImg {
    width: 28%;
}

/* Lembra de deixar essa MERDA RESPONSIVO DESSA VEZ!!!! */

@media only screen and (max-width: 1000px){
    .bodyLogin {
        display: flex;
        flex-direction: column;

        align-items: center;
        margin-top: 10%;
    }

    .machineImg {
        width: 0px;
    }
    .login {
        display: flex;
        flex-direction: column;
        background-color: #DFE2E5;
        width: 80%;
        border: 1px solid #A4C3D8 !important;
        border-radius: 5px;
        align-items: center;
        
    }

    .formsLogar input {
        margin-bottom: 5px;
        width: 90%;
    }
    
    .formsLogar button {
        width: 98%;
    }
    
    .formsLogar {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5%;
        width: 90%;
        margin-right: 5px;
        
    }
   }
   
