body {
    background-color: #f0f0f5;
    font-family: Roboto, sans-serif;
    -webkit-font-smoothing: antialiased;
}

input, textarea {
    border: 1px  #A4C3D8 solid;
    padding: 10px;
    border-radius: 15px;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
    outline: 0;
    
}
.link {
    text-decoration: none;
}

.navBar {
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
    align-items: center;
}

button {
    cursor: pointer;
    background-color: #4682B4;
    padding: 10px 25px;
    border: 0px;
    color: #ffff;
    border-radius: 20px;
    text-decoration: none;
}

